"use client"

import { useSession } from "next-auth/react"
import { useEffect, useState } from "react"
import { useRouter } from "next/navigation"
import Link from "next/link"
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { JobCard } from "@/components/job-card"

interface Job {
  id: string
  title: string
  description: string
  location: string
  salary?: string | null
  jobType: string
  experience?: string | null
  skills?: string | null
  postedAt: Date
  company: {
    id: string
    name: string
  }
  _count: {
    applications: number
  }
}

export default function MyJobsPage() {
  const { data: session, status } = useSession()
  const router = useRouter()
  const [jobs, setJobs] = useState<Job[]>([])
  const [loading, setLoading] = useState(true)

  useEffect(() => {
    if (status === "unauthenticated") {
      router.push('/login')
    } else if (session && !session.user?.isEmployer) {
      router.push('/profile')
    } else if (session) {
      fetchJobs()
    }
  }, [session, status, router])

  const fetchJobs = async () => {
    try {
      const response = await fetch('/api/my-jobs', { credentials: 'include' })
      if (response.ok) {
        const data = await response.json()
        setJobs(data.jobs)
      }
    } catch (error) {
      console.error('Error fetching jobs:', error)
    } finally {
      setLoading(false)
    }
  }

  if (loading) {
    return (
      <div className="flex items-center justify-center min-h-[400px]">
        <div className="text-lg">Loading...</div>
      </div>
    )
  }

  return (
    <div className="max-w-4xl mx-auto space-y-8">
      <h1 className="text-3xl font-bold">Manage My Jobs</h1>

      {jobs.length === 0 ? (
        <div className="text-center py-12 text-gray-500">
          <p>You have not posted any jobs yet.</p>
          <Button onClick={() => router.push('/post-job')}>
            Post Your First Job
          </Button>
        </div>
      ) : (
        <div className="space-y-6">
          {jobs.map((job) => (
            <div key={job.id}>
              <JobCard job={job} />
              <div className="mt-2 flex justify-end">
                <Link href={`/my-jobs/${job.id}/applicants`}>
                  <Button variant="outline" size="sm">
                    View Applicants ({job._count.applications})
                  </Button>
                </Link>
              </div>
            </div>
          ))}
        </div>
      )}
    </div>
  )
}
