import Link from "next/link"
import { Card, CardContent, CardHeader } from "@/components/ui/card"
import { Badge } from "@/components/ui/badge"

interface JobCardProps {
  job: {
    id: string
    title: string
    description: string
    location: string
    salary?: string | null
    jobType: string
    experience?: string | null
    skills?: string | null
    postedAt: Date
    company: {
      id: string
      name: string
      location?: string | null
    }
    _count: {
      applications: number
    }
  }
}

export function JobCard({ job }: JobCardProps) {
  const timeAgo = (date: Date) => {
    const now = new Date()
    const diffInHours = Math.floor((now.getTime() - date.getTime()) / (1000 * 60 * 60))
    
    if (diffInHours < 24) {
      return `${diffInHours}h ago`
    } else {
      const diffInDays = Math.floor(diffInHours / 24)
      return `${diffInDays}d ago`
    }
  }

  return (
    <Card className="hover:shadow-md transition-shadow cursor-pointer">
      <Link href={`/jobs/${job.id}`}>
        <CardHeader className="pb-3">
          <div className="flex items-start justify-between">
            <div className="space-y-1">
              <h3 className="text-xl font-semibold text-gray-900 hover:text-blue-600 transition-colors">
                {job.title}
              </h3>
              <p className="text-gray-600">{job.company.name}</p>
              <p className="text-sm text-gray-500">{job.location}</p>
            </div>
            <div className="text-right space-y-1">
              {job.salary && (
                <p className="text-lg font-semibold text-green-600">{job.salary}</p>
              )}
              <p className="text-sm text-gray-500">{timeAgo(job.postedAt)}</p>
            </div>
          </div>
        </CardHeader>
        <CardContent className="pt-0">
          <p className="text-gray-700 mb-4 line-clamp-2">
            {job.description.substring(0, 150)}...
          </p>
          
          <div className="flex flex-wrap gap-2 mb-4">
            <Badge variant="secondary">{job.jobType}</Badge>
            {job.experience && (
              <Badge variant="outline">{job.experience}</Badge>
            )}
            {job.skills && job.skills.split(',').slice(0, 3).map((skill, index) => (
              <Badge key={index} variant="outline">
                {skill.trim()}
              </Badge>
            ))}
          </div>

          <div className="flex items-center justify-between text-sm text-gray-500">
            <span>{job._count.applications} applications</span>
            <span className="text-blue-600 hover:text-blue-800">View Details â†’</span>
          </div>
        </CardContent>
      </Link>
    </Card>
  )
}
